import React from 'react';
import styles from './Header.module.css';

function Header() {
    return (
        <header>
                    <nav>

                        <a href="/">
                            <img src="/images/icon.png" alt=" " className={styles.label}/>
                        </a>

                        <ul>
                            <li><a href="/"></a></li>
                            <li><a href="/about"></a></li>
                            <li><a href="/classifier"></a></li>
                            <li><a href="/contact"></a></li>
                        </ul>
                    </nav>
        </header>
    );
}

export default Header;
